@import './reset.scss';
@import './base.scss';
@import './navigator.scss';

.swiper-container{
  &.pc{
    .image-wrapper{
      max-width: 1000px;
      height: 450px;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      background-position: center;
      background-size: cover;
      padding: 40px;
      .name{
        color: #FFF;
        letter-spacing: 8px;
      }
      >.title{
        font-size: 64px;
        line-height: 150%;
        font-weight: 600;
      }
      >h2{
        margin-top: 60px;
      }
    }
    .iconfont{
      color: #fff;
      font-size: 34px;
      line-height: 44px;
    }
    .swiper-slide{
      width: 1000px;
      border-radius: 8px;
      overflow: hidden;
    }
    .swiper-slide-prev:after,.swiper-slide-next:after{
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
    }
    .swiper-slide-prev:after{
      background: linear-gradient(270deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.8) 100%);
    }
    .swiper-slide-next:after{
      background: linear-gradient(270deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.4) 100%);
    }
  }
  &.mobile{
    .image-wrapper{
      max-width: 1000px;
      height: 450px;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      background-position: center;
      background-size: cover;
      flex-direction: column;
      .name{
        color: #FFF;
        //text-shadow: 0 0 4px #000; 大展说不加
      }
      >.title{
        font-size: 32px;
        line-height: 150%;
        font-weight: 600;
      }
      >h3{
        margin-top: 30px;
      }

    }
    .iconfont{
      color: #fff;
      font-size: 20px;
      line-height: 44px;
    }
  }
  .swiper-button-disabled{
    display: none;
  }
  .swiper-button-prev,.swiper-button-next {
    background-image: none;
  }
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .3s ease;
}
.fade-enter, .fade-leave-to
  /* .component-fade-leave-active for below version 2.1.8 */ {
  opacity: 0;
}

section.container{
  max-width: 800px;
  margin: 0 auto;
  h2{
    font-size: 42px;
    text-align: center;
    margin: 80px 0 50px 0;
  }
}

.mobile {
  display: none;
}

@media (max-width: 499px) {
  .pc {
    display: none !important;
  }
  .mobile {
    display: block;
  }
  section.container{
    padding: 20px;
    margin-top: -30px;
    h2{
      font-size: 28px;
      text-align: center;
      margin: 40px 0;
    }
  }
}